.menu {
  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    margin-right: 10px;

    &:after {
      content: "";
      display: inline-block;
      width: 7px;
      height: 7px;
      border: 2px solid #6a6a6a;
      border-radius: 50%;
      margin-left: 10px;
    }

    &:last-child {
      margin-left: 0;
    }
  }

  &__link {
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.35px;
  }
}
