body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
  padding: 0;
}

.wrapper {
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.container {
  width: 1140px;
  max-width: 100%;
  margin: 0 auto;
  height: 100%;
}
