.navigation {
  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    margin-right: 25px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    color: #1d1e1e;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.7px;
    color: #009ee2;
  }
}
