.top-bar {
  &__content {
    display: flex;
    align-items: center;

    .logo {
      margin-right: 30px;
    }
  }

  &__input {
    margin-right: 40px;
  }

  &__options-list {
    .options-list {
      &__bar {
        display: flex;
        align-items: center;
      }

      &__item {
        margin-right: 70px;

        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
}
