.header {
  background-color: #1d1e1e;
  padding: 10px 0;
}

.header .container {
  display: flex;
  align-items: center;
}

.header__info {
  color: #ffffff;
  font-family: "Panton";
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 0.33px;
}

.header__menu {
  margin-right: auto;
}

.logo__link {
  display: inline-block;
  color: #0086c0;
  font-family: "Panton - Black";
  font-size: 50px;
  font-weight: 400;
  letter-spacing: 4.5px;
}

.menu__list {
  display: flex;
  align-items: center;
}

.menu__item {
  margin-right: 10px;
}

.menu__item:after {
  content: "";
  display: inline-block;
  width: 7px;
  height: 7px;
  border: 2px solid #6a6a6a;
  border-radius: 50%;
  margin-left: 10px;
}

.menu__item:last-child {
  margin-left: 0;
}

.menu__link {
  color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.35px;
}

.navigation__list {
  display: flex;
  align-items: center;
}

.navigation__item {
  margin-right: 25px;
}

.navigation__item:last-child {
  margin-right: 0;
}

.navigation__link {
  color: #1d1e1e;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.7px;
  color: #009ee2;
}

.phone__number {
  color: #009ee2;
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 0.5px;
}

.top-bar__content {
  display: flex;
  align-items: center;
}

.top-bar__content .logo {
  margin-right: 30px;
}

.top-bar__input {
  margin-right: 40px;
}

.top-bar__options-list .options-list__bar {
  display: flex;
  align-items: center;
}

.top-bar__options-list .options-list__item {
  margin-right: 70px;
}

.top-bar__options-list .options-list__item:last-child {
  margin-right: 0;
}

body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
  padding: 0;
}

.wrapper {
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.container {
  width: 1140px;
  max-width: 100%;
  margin: 0 auto;
  height: 100%;
}